export const useAnimate = ({ callback, fps = 60, enabled = true }) => {
    let now;
    let then;
    const fpsInterval = 1000 / fps;
    const animate = () => {
        if (!enabled)
            return;
        requestAnimationFrame(animate);
        now = Date.now();
        const elapsed = now - then;
        if (elapsed > fpsInterval) {
            then = now - (elapsed % fpsInterval);
            callback();
        }
    };
    then = Date.now();
    animate();
};
